	module.exports = {"data":{"wordpressPost":{"title":"Five minutes with … Hair guru Tabatha Coffey","content":"<p>She’s the superstar stylist known for her outspoken and forthright opinions. We’d love her just for that but she can also whip up a hairstyle so beautiful it will take your breathe away. The US-based Tabatha Coffey flew into Australia recently where she wowed crowds at Hair Expo Australia. Plus, she spoke to us. #girlcrush</p>\n<p><strong>We&#8217;ve got makeup. Hair &#8230; That important?</strong></p>\n<p>Hair is your best accessory. If it doesn’t look good, you don’t feel good. But if we have a good hair day, we have a good day all round. You can wear the world’s most daggy outfit, but if you’ve got great hair &#8211; you’ll feel like you’ve got it going on.</p>\n<p><strong>Okay, we’re teasing. Of course it&#8217;s important. What styles will we be seeing this Autumn/Winter?</strong></p>\n<p>We’re seeing a lot of heavier fringes coming in. It’s got a 60s, early 70s vibe to it. Hair isn’t as straight anymore &#8211; it’s got a little bit more texture to it, it looks relaxed and lived in.</p>\n<p><strong>What if I&#8217;m hopeless at hair? Any simple styles everyone can manage?</strong></p>\n<p>If you’re really hopeless at doing your hair then a great haircut is priority No One. If you have a great haircut then you’re halfway there &#8211; you need a great shape as a starting point. The second most important is great products. You don’t need to fuss with stylers and curlers if you have quality products.</p>\n<p>Or try a braid &#8211; they’re so on trend right now or even a sleek ponytail with a bit of body.</p>\n<p><strong>My hair is dry and flat. What I want is silky smooth and glossy …</strong></p>\n<p>Start by using a protector spray if you’re using a flat iron or styler. Use a mask weekly and also ask your stylist about an intensive moisture treatment next time you’re in the salon.</p>\n<p><strong>I want long hair! But it doesn&#8217;t seem to want to grow past my shoulders &#8230; Am I imagining things? How do I make it happen?</strong></p>\n<p>Make sure you trim, even if it’s every three months to ensure you’re cutting the ends off. Make sure you take care of it and be patient! You need to be patient to go through the whole hair growth cycle and for some people that just takes a long time.</p>\n<p><strong>Short hair is less work &#8211; true or false? And why?</strong></p>\n<p>Totally false! Short hair is really unforgiving. When it grows, the cut loses it’s shape much quicker than most of the longer styles. If you’ve got long hair you can chuck it up in a ponytail or put a cute braid in or throw it in a bun. When you’re a short haired girl you don’t have those options. If you’ve got short hair, the key to keeping it looking great all the time is getting it styled on time.</p>\n<p><strong>How do I know what style will really suit me?</strong></p>\n<p>Consulting with a professional is really helpful, however you also need to be honest. You’re not going to look like Kim Kardashian without a lot of money and a lot of plastic surgery. We have to be realistic about our expectations. If you have baby, fine hair, you can’t look at a picture and expect to have thick, voluminous hair &#8211; that’s just not your hair type. So it’s being honest, consulting a professional and also being honest about the work you want to put into it. We can all have pretty fabulous hair we spend the time and effort that required. A lot of us just don’t have the time or the patience to do that.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/02/02145905/8d8a4d02d4eb249c173976c1b028433b-1.jpg"},"categories":[{"slug":"hair","name":"Hair"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"58e7491d-6820-5716-b22b-bc0d636dd18c"}}

